<template>
    <div class="pokemon-creation">
        <h1>Cr√©ation d'un pokemon</h1>
        <form>
            <div class="line">
                <label for="name">Nom</label>
                <input type="text" id="name" name="name" v-model="form.name" required>
            </div>
            <div class="line">
                <label for="type">Type</label>
                <input type="text" id="type" name="type" v-model="form.type" required>
            </div>
            <div class="line">
                <label for="level">Niveau</label>
                <input type="number" id="level" name="level" v-model="form.level" required>
            </div>
            <div class="line">
                <label for="image">URL de l'image</label>
                <input type="text" id="image" name="image" v-model="form.image">
            </div>
            <div class="line">
                <button type="button" @click="submitForm">Cr√©er</button>
            </div>
        </form>
    </div>
</template>
<script>
import axios from 'axios';
export default {
    data(){
        return{
            form: {
                name: '',
                type: '',
                level: '',
                image: ''
            },
            emits: ['updatedpkm']
        }
    },
    methods: {
        
        submitForm(){
            axios.post('/addpokemon', this.form)
            .then(response => {
                if(response.status === 200){
                    console.log(response);
                    axios.get('/getpokemon')
                    .then(response => {
                        this.$emit('updatedpkm', response.data);
                    })
                }else{
                    alert(`Erreur ${response.status} : ${response.statusText}`)
                }
                
            })
            .catch(error => {
                alert(error.message+"\nVous devez remplir tous les champs (sauf image). Vous ne pouvez pas ajouter de doublon.");
            })
            
        }
    }
}

</script>