<template>
    <div class="show-pokemon">
        <h1>Liste de pok√©mon</h1>
        <div class="line">
        <label for="search">üîé</label>
        <input type="search" id="search" name="search" v-model="search" placeholder="Rechercher un pok√©mon" v-on:keyup="searchpkm">
      </div>
        <div class="pkmcontainer" >
            <div class="pkmcard" v-for="pokemon in pokemons" :key="pokemon.id" >
                <img :src="pokemon.image" class="pokemonimg" v-if="editingid!=pokemon.id"/>
                <p v-if="editingid!=pokemon.id">{{ pokemon.name }}</p>
                <p v-if="editingid!=pokemon.id">Type {{ pokemon.type }}</p>
                <p v-if="editingid!=pokemon.id">Niveau {{pokemon.level}}</p>
                <button type="button" @click="editpkm(pokemon.id)" v-if="editingid!=pokemon.id">‚úèÔ∏èModifier</button>
                <button type="button" @click="deletepkm(pokemon.id)" v-if="editingid!=pokemon.id">üóëÔ∏èSupprimer</button>
                <form v-if="editingid==pokemon.id">
                  <div class="line">
                      <label for="name">Nom</label>
                      <input type="text" id="name" name="name" v-model="form.name" required :placeholder="pokemon.name">
                  </div>
                  <div class="line">
                      <label for="type">Type</label>
                      <input type="text" id="type" name="type" v-model="form.type" required :placeholder="pokemon.type">
                  </div>
                  <div class="line">
                      <label for="level">Niveau</label>
                      <input type="number" id="level" name="level" v-model="form.level" required :placeholder="pokemon.level">
                  </div>
                  <div class="line">
                      <label for="image">URL de l'image</label>
                      <input type="text" id="image" name="image" v-model="form.image">
                  </div>
                  <div class="line">
                      <button type="button" @click="editingid=null">Annuler</button>
                      <button type="button" @click="submitForm">Enregistrer</button>
                  </div>
              </form>
            </div>
        </div>
    </div>
</template>
<script>
import axios from 'axios';
export default {
    data () {
    return {
      pokemons: [],
      editing: false,
      form: {
                name: '',
                type: '',
                level: '',
                image: ''
            },
      editingid: null,
      search: '',
      counter: 0
    }
  },
  methods: {
    deletepkm(id){
      console.log(id)
        axios.delete('/deletepokemon/' + id)
        .then(response => {
            console.log(response);
            axios.get('/getpokemon/'+this.search)
            .then(response => {
                this.pokemons = response.data;
            })
        })
        .catch(error => {
            alert(error);
        })
    },
    searchpkm(){
      
      axios.get('/getpokemon/'+this.search)
      .then(response => {
          this.pokemons = response.data;
      })
      .catch(error => {
          alert(error.message+"\nLa recherche a √©chou√©.");
      })
    },
    editpkm(id){
      console.log(id)
      this.editing = true
      this.editingid=id
      
    },
    submitForm(){
      axios.put('/updatepokemon/'+this.editingid, this.form)
      .then(response => {
          console.log(response);
          axios.get('/getpokemon/'+this.search)
          .then(response => {
              this.pokemons = response.data;
              this.editing = false
              this.editingid = null
          })
      })
      .catch(error => {
          alert(error.message+"\nLa modification a √©chou√©.\nVous ne pouvez pas utiliser le m√™me nom qu'un autre pok√©mon. Vous devez remplir tous les champs (sauf image).");
      })
    }
  },
  mounted () {
    axios
      .get('/getpokemon/'+this.search)
      .then(response => (this.pokemons = response.data))
      .catch(error => { alert(error)

    })
  },
  
  
}

</script>
