<template>
  <CreatePokemon @updatedpkm="reloadview"/>
  <ShowPokemon :key="counter"/>
</template>

<script>
import CreatePokemon from './components/PokemonCreation.vue'
import ShowPokemon from './components/ShowPokemon.vue'
export default {
  name: 'App',
  components: {
    CreatePokemon,
    ShowPokemon
  },
  data(){
    return{
      counter:0
    }
  },
  
  methods: {
    
    reloadview(){
      this.counter++

    }
  }
  
}
</script>

